<template>
	<div>
		<div style="max-height:185px;overflow: auto;">
			<table class="x-cart-table">			
				<tr v-for="item in data">
					<td>
						<div class="ivu-table-cell">
							<Checkbox :value="gids.indexOf(item.gid)>-1" @on-change="onChange($event,item)"></Checkbox>
						</div>
					</td>
					<td>
						<div class="ivu-table-cell">
							<div class="cart-item clearfloat">
								<div class="thumb-img pull-left">
									<img src="../../img/good1.png">	
								</div>
								<div class="pull-left ml-3 cart-item-content">
									<span class="cart-item-title">{{item.title}}</span>
									<span class="cart-item-desc" :title="item.desc">{{item.desc}}</span>
									<div class="mt-2">
										<InputNumber v-model="item.num" :min="0" :max="9999"></InputNumber>
									</div>
								</div>
							</div>
						</div>
					</td>
					<td>
						<div class="ivu-table-cell text-right">					
							<Button size="small" type="ghost" shape="circle" icon="ios-trash"></Button>
							<p class="money mt-2">￥{{sum(item)}}</p>
						</div>
					</td>
				</tr>
			</table>
		</div>
		
		<Card dis-hover bordered class="mt-3 b-dashed m-auto" style="width:80%;border-color:#666666">
			<div class="text-center money font-weight-bold">
				￥{{amount}}
			</div>
			<Row class="mt-2">
				<Col span="12">
					<div class="pay-item">
						<div class="item-img">
							<img src="../../img/qrcode.png">
						</div>
						<Button class="mt-3" type="primary">微信支付</Button>
					</div>
				</Col>
				<Col span="12">
					<div class="pay-item">
						<div class="item-img">
							<img src="../../img/alipay.png">
						</div>
						<Button class="mt-3" type="primary">支付宝支付</Button>
					</div>
				</Col>
			</Row>			
		</Card>
	</div>
</template>

<script>
	export default{
		data () {
			return {
				data: [{
					gid: 1,
					unit: 200,
					num: 1,
					img: '',
					title: '考勤读卡器',
					desc: '即插即用无需安装'
				},
				{
					gid: 2,
					unit: 200,
					num: 1,
					img: '',
					title: '小票打印机',
					desc: '热敏打印吊炸天'
				}],
				gids: [1,2]
			}
		},
		methods: {
			onChange (val,row) {
				let gids = this.gids,
					index = gids.indexOf(row.gid)
					
				if(val&&index===-1) {					
					this.gids.push(row.gid)					
				}
				if(index>-1&&!val){
					this.gids.splice(index,1)
				}
			
			},
			sum (row) {
				let num = row.num,
					unit = row.unit
				return parseFloat(num*unit).toFixed(2)
			}
		},
		computed: {
			amount () {
				let gids = this.gids,
					result = 0
				gids.forEach(g=>{
					result = parseFloat(result) + parseFloat(this.sum(this.data.find(d=>d.gid===g)))
				})
				return parseFloat(result).toFixed(2)
			}
		}
	}
</script>



